.container {
  
  background-color: darkslategray;
}

drawer {
    z-index: 2;
    position: absolute;
    top: 0px;
    bottom: 0px;
    transition: -webkit-transform 0.3s ease-out;
    will-change: transform;
    background-color: white;
    overflow-y: auto;
    left: 0px;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    box-shadow: rgba(0, 0, 0, 0.14902) 2px 2px 4px;
}

.sidebarClass {
    z-index: 4 !important;
}

.overlayClass {
    z-index: 3 !important;
}
.image {
    padding-left: 70px;
}

.linkFade {
    -webkit-transition: color .5s ease-out;
    transition: color .5s ease-out;
}

.linkFade:hover {
  color: #BE5108;
  cursor: pointer;
}

.github-footer {
	position: absolute;
	padding-left: 70px;
    bottom: 20px;
}
